@import '~Styles/colors.module';
@import '~Styles/mixins.module';



.page_container {
  overflow: scroll;
  color: $primary-text-color;

  .order_details_container {
    padding: 13px 0px;

    .order_id_text {
      opacity: 0.9;
      font-size: 16px;
      font-weight: 300;
    }

    .order_status_text {
      font-size: 12px;
      color: $color-green-apple;
      text-transform: capitalize;
    }

    .order_placed_text {
      opacity: 0.8;
      font-size: 12px;
    }
  }

  .content_container {
   border-top: 1px solid rgba(black, 0.05); 
   flex: 1;

   .left_container {
    flex: 1;

    .top_header_text {
      align-self: stretch;
      padding: 17px 0px 17px 49px;
      font-size: 12px;
      border-bottom: 1px solid rgba(black, 0.05); 
    }

    .contact_text:first-child {
      margin-bottom: 5px;
    }

    .contact_text {
      opacity: 0.8;
      font-size: 14px;
    }
    
    .header_text {
      font-size: 12px;
      padding: 9px 0px 9px 49px;
      border-top: 1px solid rgba(black, 0.05);
      border-bottom: 1px solid rgba(black, 0.05);
    }

    .value_container {
      padding: 11px 0px 40px 49px;
    }

    .name_text {
      font-size: 14px;
      font-weight: bold;
      margin-bottom: 3px;
    }

    .address_text {
      font-size: 12px;
    }

    .price_title {
      opacity: 0.8;
      font-size: 12px;
      min-width: 100px;
    }

    .value {
      opacity: 0.8;
      font-size: 12px;
    }
   }


   //-----------------------------------------------rightContainer


   .right_container {
     flex: 1;
     border-left: 1px solid rgba(black, 0.05);

     .top_header_text {
        align-self: stretch;
        padding: 13px 0px 14px 49px;
        font-size: 16px;
        border-bottom: 1px solid rgba(black, 0.05); 
      }

      .product_item_container {
        margin: 9px 0px 11px 49px;

        .product_image {
          width: 124px;
          height: 126px;
        }

        .product_content_container {
          margin-left: 14px;

          .product_name {
            font-size: 14px;
            font-weight: bold;
          }

          .product_type {
            font-size: 12px;
            font-weight: 300;
            text-transform: capitalize;
          }

          .product_price {
            font-size: 18px;
            font-weight: 900;
            color: black;
          }
        }
      }

      .exhibition_container {
        padding: 13px 0px 0px 49px;
        border-top: 1px solid rgba(black, 0.05);

        .exhibition_title {
          font-size: 12px;
          margin-bottom: 11px;
        }

        .exhibition_image {
          width: 51px;
          height: 52px;
          margin-right: 12px;
        }

        .exhibition_name {
          font-size: 15px;
          
        }
      }

   }
  }
}


.rtl {
  .content_container {
    text-align: right;

    .left_container {

     .top_header_text {
       padding: 17px 49px 17px 0px;
     }
     
     .header_text {
       padding: 9px 49px 9px 0px;
     }
 
     .value_container {
      padding: 11px 49px 40px 0px;
      align-items: flex-end;
     }
    }
 
    //-----------------------------------------------rightContainer
   
    .right_container {
 
      .top_header_text {
         padding: 13px 49px 14px 0px;
       }
 
       .product_item_container {
         margin: 9px 49px 11px 0px;
         flex-flow: row-reverse;
 
         .product_content_container {
          margin-left: 0px;
          margin-right: 14px;
         }
       }
 
       .exhibition_container {
         padding: 13px 49px 0px 0px;
 
         .exhibition_title {
           margin-bottom: 11px;
         }
 
         .exhibition_image {
           margin-right: 12px;
         }
 
         .exhibition_name {
         }
       }
 
    }
   }
}

@include small {
  .page_container {  
    .content_container {
      flex-direction: column;
    }
  }
}