@import '~Styles/colors.module';
@import '~Styles/mixins.module';

.header_container {
  align-self: stretch;
  padding: 11px 46px;
  justify-content: space-between;
  border-bottom: 0.5px solid #ededed;
  margin-top: 10px;
  
  .header_title {
    font-size: 12px;
    color: $primary-text-color;
    text-transform: uppercase;
    cursor: pointer;
  }

  .back_icon {
    margin-right: 13px;
    height: 10px;
    transform: scaleX(-1);
  }
}


@include medium {
  .header_container {
    padding: 10px 36px;
    margin-top: 8px;
    
    .header_title {
      font-size: 12px;
    }
  
    .back_icon {
      margin-right: 10px;
      height: 10px;
    }
  }
}

@include small {
  .header_container {
    padding: 8px 26px;
    margin-top: 10px;
    
    .header_title {
      font-size: 10px;
    }
  
    .back_icon {
      margin-right: 10px;
      height: 8px;
    }
  }
}
