@import '~Styles/colors.module';
@import '~Styles/mixins.module';

.page_container {
  overflow-y: scroll;
  justify-content: flex-start;
}

.orders_container {
  flex: 1;
  align-self: stretch;
  margin: 0px 36px 36px;
  // max-height: 248px;

  .list_header {
    align-self: stretch;
    border: solid 1px rgba($primary-text-color, 0.1);
    padding: 11px 23px;
    justify-content: space-between;
  }

  .header_text {
    opacity: 0.9;
    font-size: 10px;
    font-weight: 300;
    color: $primary-text-color;
  }

  .order_item_container {
    height: 205px;
    margin-top: 10px;
    align-self: stretch;
    border-right: solid 0.5px rgba($primary-text-color, 0.1);
    border-bottom: solid 0.5px rgba($primary-text-color, 0.1);


    .order_image {
      width: 205px;
      height: 205px;
    }


    .order_detail_container {
      flex:1 ;
      padding: 0px 18px 0px 12px;

      .order_exhibition {
        font-size: 18px;
        font-weight: bold;
        color: $primary-text-color;      
        margin: 18px 0px 7px;
      }
  
      .order_name {
        font-size: 14px;
        font-weight: 300;
        color: $primary-text-color;
      }
  
      .order_price {
        opacity: 0.9;
        font-size: 18px;
        font-weight: 900;
        color: black;      
      }
  
      .order_state {
        font-size: 12px;
        color: $color-green-apple;
        margin-top: 25px;
      }
  
      .view_order_button {
        border-radius: 100px;
        background-color: $capsule-button-color;
        font-size: 14px;
        font-weight: 900;
        padding: 6px 20px;
        color: white;
        align-self: flex-end;
        @include on_click_button;
      }
  
    }
  }
}

.rtl {
  .order_item_container {   
    flex-flow: row-reverse;

    .order_detail_container {
      text-align: right;
    
      .view_order_button {
        align-self: flex-start;
      }
    }
  }
}

@include medium {
  .orders_container {
    margin: 0px 30px 30px;

    .order_item_container {
      height: 180px;
      margin-top: 10px;
    
      .order_image {
        width: 180px;
        height: 180px;
      }
    }
  }
}

@include small {
  .orders_container {
    margin: 0px 15px 15px;
  
    .header_text {
      font-size: 10px;
    }
  
    .order_item_container {
      flex-direction: column;
      align-items: center;
      height: unset;
      margin-top: 10px;
      padding: 10px 0px;

      .order_image {
        width: 150px;
        height: 150px;
      }
  
  
      .order_detail_container {
        padding: unset;
        text-align: center;
  
        .order_exhibition {
          font-size: 16px;
          font-weight: bold;
          color: $primary-text-color;      
          margin: 10px 0px 7px;
        }
    
        .order_name {
          font-size: 12px;
        }
    
        .order_price {
          font-size: 16px;
        }
    
        .order_state {
          font-size: 12px;
          margin-top: 20px;
        }
    
        .view_order_button {
          font-size: 12px;
          padding: 4px 15px;
          margin-top: 8px;
        }
    
      }
    }
  }
}